.bubble-slider {
  display: block;
  width: 100%;
  margin: 20px auto;
  padding: 20px;
  font-size: 16px;
}
.bubble-slider-wrap {
  position: relative;
  margin: 55px auto;
  display: block;
  width: 100%;
  height: 55px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.bubble-slider-wrap::before,
.bubble-slider-wrap::after {
  content: '';
  display: table;
}
.bubble-slider-wrap::after {
  clear: both;
}
.bubble-slider-track {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
  left: 40px;
  right: 40px;
  height: 2px;
  background: #e5e5e5;
}
.bubble-slider-thumb {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
  width: 50px;
  height: 50px;
  background: #595959;
  border-radius: 50%;
  cursor: ew-resize;
}
.bubble-slider-thumb span:first-child {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #eee;
  font-size: 16px;
}
.bubble-slider-bubble {
  position: absolute;
  left: 50%;
  top: -70%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 15% 25%;
  font-size: 25px;
  color: #bdc3c3;
  background: #343a3a;
  border-radius: 7px;
}
.bubble-slider-bubble span {
  position: relative;
}
.bubble-slider-bubble-arrow {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0) rotate(45deg);
      -ms-transform: translate(-50%, 0) rotate(45deg);
          transform: translate(-50%, 0) rotate(45deg);
  bottom: -10%;
  width: 10px;
  height: 10px;
  background: #343a3a;
}
.bubble-slider-minus,
.bubble-slider-plus {
  position: relative;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
  width: 30px;
  height: 30px;
  color: #595959;
  font-size: 18px;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
  cursor: pointer;
}
.bubble-slider-minus {
  float: left;
}
.bubble-slider-plus {
  float: right;
}
.bubble-slider-minus span {
  position: absolute;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.bubble-slider-plus span {
  position: absolute;
  left: 51%;
  top: 51%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
